li {
  list-style: none;
}

.messages-list {
  height: 100%;
  padding-top: 30px;
  padding-left: 0;
  margin-bottom: 0;
  margin-top: 0;
  margin-left: 20px;
  overflow-y: auto;
  overflow-x: hidden;
  word-wrap: break-word;
  white-space: pre-wrap;
  box-sizing: border-box;
}

.wrapper__title {
  text-align: center;
  color: #472D74;
  font-weight: bold;
  margin-bottom: 0;
  margin-top: 0;
  padding-bottom: 10px;
  word-break: break-word;
  white-space: pre-line;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 6px;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #472D74;
  cursor: pointer;
}

::-webkit-scrollbar-track {
  background-color: #979BDB;
}

.message-description {
  margin-bottom: 0;
  margin-top: 5px;
  display: flex;
  align-items: baseline;
}

.message-author {
  color: #7C80BD;
  font-weight: bold;
  word-break: break-word;
  white-space: pre-line;
}

.message-author--highlighted:before {
  content: url("./img/crown.png");
  width: 5px;
  height: 5px;
}

.message-time {
  margin-left: 10px;
  color: #a9a9a9;
  font-size: 11px;
}

.message-text {
  margin: 0;
}

.message-date {
  text-align: center;
  color: #666666;
  position: sticky;
  top: -30px;
  background-color: #FFFFFF;
  opacity: 1;
}

ul li:nth-child(even) {
  background: rgba(253, 242, 254, .6);
}

.messages-list__new-message {
  text-align: center;
  color: #472D74;
  font-weight: bold;
}

.messages-list__new-message:before,
.messages-list__new-message:after {
  content: '';
  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  width: 43.4%;
  overflow: hidden;
  height: 2px;
  background: #472D74;
  border: solid #472D74;
  border-width: 0 .5em 0;
}

.messages-list__new-message:before {
  margin-left: -100%;
}

.messages-list__new-message:after {
  margin-right: -100%;
}

